"use client";

import React, { useState } from 'react';

export default function SearchPage() {
    const [searchName, setSearchName] = useState('');
    const [searchRating, setSearchRating] = useState('');
    const [searchYear, setSearchYear] = useState('');
    const [results, setResults] = useState([]);

    const handleSearch = async () => {
        // Mocked data for testing
        const mockData = [
            { title: "Inception", rating: 8.8, year: 2010 },
            { title: "Interstellar", rating: 8.6, year: 2014 },
            { title: "The Dark Knight", rating: 9.0, year: 2008 },
        ];

        const filteredResults = mockData.filter((item) => {
            return (
                (!searchName || item.title.toLowerCase().includes(searchName.toLowerCase())) &&
                (!searchRating || item.rating >= parseFloat(searchRating)) &&
                (!searchYear || item.year === parseInt(searchYear))
            );
        });

        setResults(filteredResults);
    };

    return (
        <div>
            <h1>Search Movies</h1>
            <div>
                <label>
                    Name:
                    <input
                        type="text"
                        value={searchName}
                        onChange={(e) => setSearchName(e.target.value)}
                    />
                </label>
                <label>
                    Rating:
                    <input
                        type="number"
                        value={searchRating}
                        onChange={(e) => setSearchRating(e.target.value)}
                        min="0"
                        max="10"
                    />
                </label>
                <label>
                    Year:
                    <input
                        type="number"
                        value={searchYear}
                        onChange={(e) => setSearchYear(e.target.value)}
                    />
                </label>
                <button onClick={handleSearch}>Search</button>
            </div>
            <div>
                <h2>Results:</h2>
                {results.length > 0 ? (
                    <ul>
                        {results.map((result, index) => (
                            <li key={index}>{result.title}</li>
                        ))}
                    </ul>
                ) : (
                    <p>No results found</p>
                )}
            </div>
        </div>
    );
}
